import React, { useEffect } from 'react';

import './Map.css'

let latitude = 35.55843592617178
let longitude = 129.09498754744544
let level = 3

declare global {
  interface Window {
    kakao: any;
  }
}

const Map = () => {
    useEffect(() => {

        let container = document.getElementById('map'); //지도를 담을 영역의 DOM 레퍼런스
        let options = { //지도를 생성할 때 필요한 기본 옵션
          center: new window.kakao.maps.LatLng(latitude, longitude), //지도의 중심좌표.
          level: 3 //지도의 레벨(확대, 축소 정도)
        };

        let map = new window.kakao.maps.Map(container, options); //지도 생성 및 객체 리턴
        let markerPosition = new window.kakao.maps.LatLng(latitude, longitude)

        let marker = new window.kakao.maps.Marker({
            position: markerPosition,
            clickable: true
        });

        // 마커가 지도 위에 표시되도록 설정합니다
        marker.setMap(map);
        let weddingLocationURL = 'https://m.map.kakao.com/actions/searchView?q=%EB%8F%99%EC%B1%84%EC%9B%90&wxEnc=MNMTPP&wyEnc=QNOQLNN&lvl=3#!/818121381/map/place';
        window.kakao.maps.event.addListener(marker, 'click', function() {
            window.open(weddingLocationURL);
        });
      }, [])

    return (
        <div className="kakao-map" id="map"/>
    );
}

export default Map;
